import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DetailCustomerDebtComponent } from './detail-customer-debt.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { TranslateModule } from '@ngx-translate/core';
import { DetailReceiptExpensesComponent } from './detail-receipt-expenses/detail-receipt-expenses.component';
import { CreateReceiptsComponent } from './create-receipts/create-receipts.component';
import { BillDetailModule } from 'src/app/sales/components/bill-detail/bill-detail.module';
const routes: Routes = [
  {
    path: '',
    component: DetailCustomerDebtComponent
  },
  {
    path: 'detail-receipt-expenses',
    component: DetailReceiptExpensesComponent
  },
  {
    path: 'create-receipt',
    component: CreateReceiptsComponent
  }
]

@NgModule({
  declarations: [
    DetailCustomerDebtComponent,
    DetailReceiptExpensesComponent,
    CreateReceiptsComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    FormsModule,
    ReactiveFormsModule,
    TranslateModule,
    BillDetailModule
  ]
})
export class DetailCustomerDebtModule { }
