import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { ReceiptA4Component } from 'src/app/sales/components/print/receipt/receipt-a4/receipt-a4.component';
import { ReceiptA5Component } from 'src/app/sales/components/print/receipt/receipt-a5/receipt-a5.component';
import { ReceiptK80Col4Component } from 'src/app/sales/components/print/receipt/receipt-k80-col4/receipt-k80-col4.component';
import { ReceiptK80Col3Component } from 'src/app/sales/components/print/receipt/receipt-k80-col3/receipt-k80-col3.component';
import { ReceiptK57Col3Component } from 'src/app/sales/components/print/receipt/receipt-k57-col3/receipt-k57-col3.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';
@Component({
  selector: 'app-create-receipts',
  templateUrl: './create-receipts.component.html',
  styleUrls: ['./create-receipts.component.scss']
})
export class CreateReceiptsComponent implements OnInit {
  type: any = '11'; // Biến lưu loại phiếu thu được chọn
  listPaymentBank: any = []; //danh sách ngân hàng thanh toán
  customer: any;
  money_currency: any;
  addReceiptForm: FormGroup;
  billInfo: any = { employee: this.vhAuth.getUser() };
  list_customers: any = [];
  nzOptions: any;
  dataRestore: any = this.router.getCurrentNavigation().extras.state;
  receiptGroup: any[] = this.vhQuerySales.getlocalIncoming_groups()
  /**Giới hạn thời gian cho tạo bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < (new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000)) return true
    return false
  }
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private vhQuery: VhQuery,
    public languageService: LanguageService,
    private navCtrl: NavController,
    private fncService: FunctionService
  ) {
    this.getPaymentBankList();
    let options = [
      {
        value: '1',
        label: this.languageService.translate('Cash'),
        isLeaf: true
      },
      {
        value: '3',
        label: this.languageService.translate('Account'),
        children: this.listPaymentBank
      }
    ]
    this.nzOptions = options;
  }
  checkPrinter: boolean = false;
  ngOnInit(): void {
    this.initForm();
    this.getCustomersList();
    let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_receipt')

    if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
  }
  ngAfterViewInit() {
    // tạo cleavejs cho input price
    this.vhAlgorithm.waitingStack().then(() => {
      this.money_currency = this.vhAlgorithm.vhnumeral(".price");
    });
  }

  /**
   * khởi tạo các trường mặc định trong addReceiptForm
   * @example this.initForm() 
   */
  initForm() {
    this.addReceiptForm = new FormGroup({
      date: new FormControl(new Date().toISOString()),
      customer: new FormControl(this.dataRestore.data._id, Validators.compose([Validators.required])),
      total: new FormControl('', Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      content: new FormControl(''),
      payment_type: new FormControl('1'),
      wallet: new FormControl(null),
      type_receipt: new FormControl(this.type),
      id_group: new FormControl(null)
    });
  }
  /**Hàm chọn loại phiếu thu*/
  chooseType(value) {
    this.initForm();
  }

  /**
   * check validate hợp lệ
   * @param $event date
   */
  changeDate(event) {
    this.vhQuerySales.getValidityDate(event)
      .then((validitydate: any) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.addReceiptForm.controls['date'].setValue(new Date())
        }
      })
  }

  /**Get tk ngân hàng và gán váo biến listPaymentBank
   * @example this.getPaymentBankList()
  */
  getPaymentBankList() {
    let listPayment = this.vhQuerySales.getlocalWallets().filter(item => item._id != 'id_cash' && item.id_branch == this.vhQuerySales.getDefaultBranch()._id);
    this.listPaymentBank = listPayment.map(item => {
      return { value: item._id, label: item.name, isLeaf: true }
    })
  }

  /**Get ds customer và gán váo biến list_customers để chọn khách cho phiếu thu
   * @example this.getCustomersList()
  */
  getCustomersList() {
    this.list_customers = this.vhQuerySales.getlocalCustomers().filter(item => item.type != 3).filter(customer => customer['_id'] != "id_retail" && customer['debt_enable'] == true);
  }

  /**trở về trang chi tiết công nợ khách hàng
   * @example this.goBack()
   */
  goBack(value?) {
    let data = { ...this.dataRestore }
    data.check = true
    this.router.navigate(['/sales/dashboard/manage/cashflow/customer-debt/detail-customer-debt'], {
      state: value ? data : this.dataRestore
    });
  }

  /**Khởi tạo các trường chuẩn của bill để lưu DB
   * @example  const bill = this.exportBill(bill, '070623F001')
   * @returns bill
  */
  exportBill(formValue, bill_code) {
    const bill: any = {
      bill_code: bill_code,
      date: new Date(formValue["date"]),
      id_employee: this.billInfo['employee']._id,
      id_branch: this.vhQuerySales.getDefaultBranch()._id,
      bill_type: parseInt(this.type),
      content: formValue["content"],
      payment_type: formValue["payment_type"],
      tax: 0,
      total: parseFloat(this.money_currency.getRawValue()),
      payment: parseFloat(this.money_currency.getRawValue())
    }

    if (bill['payment_type'][0] == '3') {
      bill['id_wallet'] = bill['payment_type'][1];
      bill['payment_type'] = 3;
    } else {
      bill['payment_type'] = 1;
      bill['id_wallet'] = 'id_cash';
    }
    if (this.type == 11) {
      bill['id_customer'] = formValue['customer']
      this.billInfo['customer'] = this.vhQuerySales.getlocalCustomer(formValue['customer'])
    } else {
      bill.name = formValue["customer"]
      bill.id_group = formValue["id_group"]
    }
    return bill;

  }

  /**Hàm tạo phiếu thu lưu vào DB
   * @param print :  biến cờ check cho in hay ko
   * @example this.addReceipt(true)
  */
  addReceipt(print) {
    Promise.all([
      this.vhQuerySales.createBillCode(new Date(this.addReceiptForm['value']["date"]), parseInt(this.type)),
      this.vhComponent.showLoading("")
    ])
      .then(([bill_code, loading]) => {
        const bill = this.exportBill(this.addReceiptForm['value'], bill_code)
        this.vhQuerySales
          .createBill(bill)
          .then((res: any) => {
            this.billInfo = { ...this.billInfo, ...bill, id: res._id }
            this.dataRestore.data.json.docs.push({ ...res, debt: 0, rest: -bill.total })
            this.dataRestore.data.json.rest -= bill.total
            this.dataRestore.data.rest -= bill.total
            this.vhComponent.showToast(1500, (this.languageService.translate("Receipt has been created successfully")), "success-toast");
            this.vhComponent.hideLoading(0).then(() => {
              if (print) this.showPrint();
              else setTimeout(() => this.goBack(true), 200)
            })
          }, err => { })
      })
  }


  /** kiểm tra dữ liệu máy in và show component in tương ứng
  * @example this.showPrint() 
  */
  showPrint() {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { billInfo: this.billInfo, printer: printer }, false, false, `modal-print-${printer['_id']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { this.goBack(true) })
          })
      }
    }, err => { this.goBack(true) })
  }
  /**
   * trả về component của trang in tương ứng
   * @example this.renderPrintPage('print_size_a4')
   * @returns ReceiptK57Col3Component | ReceiptK80Col3Component | ReceiptK80Col4Component | ReceiptA5Component | ReceiptA4Component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ReceiptK57Col3Component
      case 'print_size_k80_3c': return ReceiptK80Col3Component
      case 'print_size_k80_4c': return ReceiptK80Col4Component
      case 'print_size_a5': return ReceiptA5Component
      case 'print_size_a4': return ReceiptA4Component
      default: return ReceiptA4Component
    }
  }

  /*----------Print------------------*/
  /**
   * lấy thông tin dữ liệu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_receipt')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }

  /**Zorro modal save*/
  isVisible = false;
  isPrint: boolean;
  /**
   * hiển thị modal xác nhận tạo phiếu thu
   * @param value : biến cờ check in hay ko
   * @example this.showModal(false)
   */
  showModal(value): void {
    this.isPrint = value;
    this.isVisible = true;
  }
  /**
   * bắt sk nhấn nút ok của modal xác nhận tạo phiếu thu
   * @example this.handleOk()
   */
  handleOk(): void {
    this.isVisible = false;
    this.addReceipt(this.isPrint);
  }
  /**
   * bắt sk nhấn nút hủy của modal xác nhận tạo phiếu thu
   * @example this.handleCancel()
   */
  handleCancel(): void {
    this.isVisible = false;
  }




}
