import { DatePipe } from '@angular/common';
import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuerySales } from 'ionic-vhframeworks';
import { LanguageService } from 'src/app/sales/services/language.service';
import { SalesComponent } from 'src/app/sales/components/bill-detail/sales/sales.component';
import { WarrantyComponent } from 'src/app/sales/components/bill-detail/warranty/warranty.component';
import { CustomerReturnComponent } from 'src/app/sales/components/bill-detail/customer-return/customer-return.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { SalesVoucherComponent } from 'src/app/sales/components/bill-detail/sales-voucher/sales-voucher.component';
import { SalesPaymentCardComponent } from 'src/app/sales/components/bill-detail/sales-payment-card/sales-payment-card.component';

@Component({
  selector: 'app-detail-customer-debt',
  templateUrl: './detail-customer-debt.component.html',
  styleUrls: ['./detail-customer-debt.component.scss']
})
export class DetailCustomerDebtComponent {
  stateObj: any; // biến chưa dữ liệu từ customer-debt, để truyền về lại bằng stage
  data: any; // biến chưa dữ liệu như tên ncc, nợ lại (rest)
  bills: any = new Object(); // biến chứa dữ liệu chi tiết, truyền qua từ customer-debt(Json)
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    public languageService: LanguageService,
    public vhAuth: VhAuth,
    private cdRef: ChangeDetectorRef,
    private datePipe: DatePipe,
    private vhComponent:VhComponent
  ) {
    let state: any = this.router.getCurrentNavigation().extras.state
    if (state) {
      this.data = state.data; // lấy dữ liệu từ customer-debt
      this.stateObj = state.stateObj // lấy dữ liệu để truyền về
      this.bills = this.data.json // lấy dữ liệu chi tiết 
    }
  }



  heightScroll: string;
  ngAfterViewChecked() {
    // tính chiều cao của table để scroll
    if (document.querySelector("#product-list") && document.querySelector(".ant-table-thead") && document.querySelector(".height-info")) {
      this.heightScroll = (document.querySelector("#product-list").clientHeight - document.querySelector(".ant-table-thead").clientHeight
        - document.querySelector(".product-list-header").clientHeight - document.querySelector(".height-info").clientHeight) - 90 + 'px'
    }
    this.cdRef.detectChanges();
  }

  /**
   * trở lại trang công nợ khách hàng
   * @example this.goBack()
   */
  goBack() {
    this.router.navigate(['/sales/dashboard/manage/cashflow/customer-debt'], {
      state: { stateObj: this.stateObj }
    });
  }



  /**
   * router qua trang xem chi tiết bill theo từng loại bill-type 
   * @example this.goToBillDetail(bill)
   */
  goToBillDetail(itemDetail) {
    let state = {
      id: itemDetail['_id'],
      data: this.data,
      stateObj: this.stateObj,
      bills: { bills: this.bills }
    }
    if (itemDetail['bill_type'] == 11) {
      itemDetail['name'] = this.data['name']
      if (itemDetail['id_wallet']) itemDetail['wallet'] = this.vhQuerySales.getlocalWallet(itemDetail['id_wallet'])
      setTimeout(() => {
        this.router.navigate(['/sales/dashboard/manage/cashflow/customer-debt/detail-customer-debt/detail-receipt-expenses'], {
          state: {
            ...itemDetail,
            id: itemDetail['_id'],
            data: this.data,
            stateObj: this.stateObj,
            bills: { bills: this.bills }
          }
        })
      }, 100)
    }
    if (itemDetail['bill_type'] == 1) {
       this.vhComponent.showModal(SalesComponent, { id_bill: itemDetail._id }, false, false, 'bill-detail')
        .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
    }
    
    if (itemDetail['bill_type'] == 15) {
       this.vhComponent.showModal(WarrantyComponent, { id_bill: itemDetail._id }, false, false, 'bill-detail')
        .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
    }
    
    if (itemDetail['bill_type'] == 3) {
       this.vhComponent.showModal(CustomerReturnComponent, { id_bill: itemDetail._id }, false, false, 'bill-detail')
        .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
    }
    
    if (itemDetail['bill_type'] == 40) {
       this.vhComponent.showModal(SalesVoucherComponent, { id_bill: itemDetail._id }, false, false, 'bill-detail')
        .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
    }
    
    if (itemDetail['bill_type'] == 50) {
       this.vhComponent.showModal(SalesPaymentCardComponent, { id_bill: itemDetail._id }, false, false, 'bill-detail')
        .then(modal => {
            modal.onWillDismiss().then(dataReturn => {})
          })
    }


  }
  /**
   * chuyển dạng date '2023-06-07T01:33:38.033Z' sang string với format '07_06_2023'
   * @param date 
   * @returns string date dạng dd_MM_yyyy
   * @example this.format_date('2023-06-07T01:33:38.033Z')
   */
  format_date(date) {
    return this.datePipe.transform(date, "dd_MM_yyyy")
  }
  /** biến cờ để check mỗi lần chỉ tải về 1 lần  */
  status: boolean = false;
  /**
   * Xuát ra file .xlxs và tải xuống máy
   * @example this.exportCSV()
   */
  exportCSV() {
    this.status = true;
    let data = new Array();
    // tên file tải xuống 
    let name: any = `${this.languageService.translate("_customer_receivables_report")}_${this.vhAlgorithm.changeAlias(this.data.name).replace(/\s/g, "_")}_${this.stateObj.id_branch_report == "all" ? "" : this.vhAlgorithm.changeAlias(this.vhQuerySales.getlocalBranch(this.stateObj.id_branch_report).name).replace(/\s/g, "_")}`;
    // dòng đầu tiên của file
    let total: any = new Object();
    total[this.languageService.translate("No.")] = "";
    total[this.languageService.translate("Invoice code")] = "";
    total[this.languageService.translate("Date")] = this.languageService.translate("Total");
    total[this.languageService.translate("Employee")] = "";
    total[this.languageService.translate("Amount debt")] = this.vhAlgorithm.vhcurrencyunit_symbol(this.bills.debt);
    total[this.languageService.translate("Amount paid")] = this.vhAlgorithm.vhcurrencyunit_symbol(this.bills.payment);
    total[this.languageService.translate("Amount rest")] = this.vhAlgorithm.vhcurrencyunit_symbol(this.bills.rest);
    total[this.languageService.translate("Note")] = "";
    data.push(total);

    let beginning_debt: any = new Object()
    beginning_debt[this.languageService.translate("No.")] = "";
    beginning_debt[this.languageService.translate("Invoice code")] = "";
    beginning_debt[this.languageService.translate("Date")] = this.languageService.translate("Debt at the beginning of the period");;
    beginning_debt[this.languageService.translate("Employee")] = "";
    beginning_debt[this.languageService.translate("Amount debt")] = this.vhAlgorithm.vhcurrencyunit_symbol(this.bills.debt_beginning);
    beginning_debt[this.languageService.translate("Amount paid")] = "";
    beginning_debt[this.languageService.translate("Amount rest")] = this.vhAlgorithm.vhcurrencyunit_symbol(this.bills.debt_beginning);
    beginning_debt[this.languageService.translate("Note")] = ""

    data.push(beginning_debt);

    // danh sách dữ liệu cho các dòng tiếp theo
    for (let i in this.bills.docs) {
      let item: any = new Object();
      item[this.languageService.translate("No.")] = parseInt(i) + 1;
      item[this.languageService.translate("Invoice code")] = this.bills.docs[i].bill_code;
      item[this.languageService.translate("Date")] = this.format_date(this.bills.docs[i].date);
      item[this.languageService.translate("Employee")] = this.vhAuth.getlocalEmployee(this.bills.docs[i].id_employee).name
      item[this.languageService.translate("Amount debt")] = this.vhAlgorithm.vhcurrencyunit(this.bills.docs[i].debt);
      item[this.languageService.translate("Amount paid")] = this.vhAlgorithm.vhcurrencyunit(this.bills.docs[i].payment);
      item[this.languageService.translate("Amount rest")] = this.vhAlgorithm.vhcurrencyunit(this.bills.docs[i].rest);
      item[this.languageService.translate("Note")] = this.bills.docs[i].content;
      data.push(item);
    }
    this.vhAlgorithm.exportXLSX(data, name).then(() => this.status = false).catch(err => this.status = false)
  }

  /**
   * nhận sk click tạo phiếu thu, router qua trang tạo phiếu thu
   * @example this.gotoCreateReceipts()
   */
  gotoCreateReceipts() {
    let state = { // khai báo dữ liệu truyền đi cho từng trang chi tiêt
      data: this.data,
      stateObj: this.stateObj,
      bills: { bills: this.bills }
    }
    this.router.navigate(['/sales/dashboard/manage/cashflow/customer-debt/detail-customer-debt/create-receipt'], {
      state: state
    })
  }
}
